@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  font-family: 'Raleway', sans-serif;
  margin: 0;
  background-color: black;
  background: rgb(123,143,148);
  background: repeating-linear-gradient(45, rgba(123,143,148,1) 0%, rgba(108,114,115,1) 48%, rgba(123,143,148,1) 100%);
}

.vh-50 {
  height: 50vh;
}

.ph-1r {
  padding: 0rem 1rem;
}
.hero{height:50vh}.jumbotron{min-height:350px;max-height:400px;padding-bottom:0}.section{max-width:1200px;margin:auto;margin-bottom:2rem}.recent-posts{display:flex;flex-wrap:wrap;justify-content:space-evenly;max-width:1200px;margin:auto}@media screen and (min-width: 700px){.about-text{width:50%}}

.post{background-color:#303030;background-clip:border-box;border:1px solid rgba(0,0,0,0.125);border-radius:.25rem;border-color:#00bc8c !important}.blog-posts-list{display:flex;flex-direction:column;align-items:flex-end}.post-img{border-radius:.25rem;border:1px solid rgba(0,0,0,0.125)}.img-container{border:1px solid rgba(0,0,0,0.125);border-radius:.25rem}@media screen and (min-width: 700px){.post{margin-bottom:1rem;padding:1rem;width:60%;display:flex;height:300px}.post .img-container{height:200px;align-self:center}.post .img-container .post-img{height:100%}.post .post-content-preview{height:250px;padding:10px;align-self:center}.post .post-content-preview p{line-height:30px}}

body{background-color:#222}.navbar{padding:.25rem 1rem}

